<template>
  <div class="detail">
    <b ref="detail" v-show="flag" @click="close"></b>
    <article>
      <el-row :gutter="30">
        <el-col :span="16">
          <section>
            <h3>
              <p>{{headTxt}}</p>
              <a href="#/" class="first">返回首页 /</a>
              <a :href="'#/msgList/'+type" class="last">返回列表页 /</a>
              <span>详情</span>
            </h3>
            <div class="content">
              <h4>{{obj.title}}</h4>
              <span>{{obj.updatedAt|format}}</span>
              <video v-if="tableName==='Video'" :src="obj.url" controls />
              <aside class="img" v-if="tableName==='Images'">
                <img v-for="src in obj.srcArr" :src="src" @click="showBig(src)" :key="src" />
              </aside>
              <img :src="obj.src" v-if="tableName==='Adv'" />
              <aside
                class="con"
                v-if="tableName==='News' || tableName==='Adv'"
                v-html="obj.content"
              ></aside>
              <p v-else>{{obj.content}}</p>
            </div>
          </section>
        </el-col>
        <el-col :span="8">
          <Panel />
        </el-col>
      </el-row>
    </article>
  </div>
</template>
<script>
import { Url } from "../components/public";
import Panel from "../components/Panel";
export default {
  name: "Detail",
  data() {
    return {
      query: null,
      type: "",
      headTxt: "",
      obj: {},
      url: Url,
      flag: false,
      img: null,
      tableName: ""
    };
  },
  mounted() {
    this.query = this.$route.query;
    this.getObj();
  },
  methods: {
    getObj() {
      const type = this.query.type;
      this.type = type;
      const id = this.query.id;
      let tableName = "";
      switch (type) {
        case "headlines":
          this.headTxt = "关注头条";
          tableName = "News";
          break;
        case "comprehensive":
          this.headTxt = "综合新闻";
          tableName = "News";
          break;
        case "learning":
          this.headTxt = "学术动态";
          tableName = "News";
          break;
        case "story":
          this.headTxt = "师大故事";
          tableName = "News";
          break;
        case "notice":
          this.headTxt = "通知公告";
          tableName = "Notice";
          break;
        case "Video":
          this.headTxt = "视频新闻";
          tableName = "Video";
          break;
        case "Images":
          this.headTxt = "图片新闻";
          tableName = "Images";
          break;
        case "newest":
          this.headTxt = "最新新闻";
          tableName = "News";
          break;
        case "newhot":
          this.headTxt = "热点新闻";
          tableName = "News";
          break;
        case "adv":
          this.headTxt = "广告新闻";
          tableName = "Adv";
          break;
      }
      this.tableName = tableName;
      const params = { params: { tableName, id } };
      this.$axios.get("getDetail", params).then(obj => {
        if (obj.photoes) {
          const arr = obj.photoes.split(",");
          arr.splice(arr.length - 1, 1);
          obj.srcArr = arr.map(el => {
            return Url + "photoes/" + el;
          });
        }
        if (obj.url) {
          obj.url = Url + "video/aaa.mp4";
        }
        if (this.tableName === "Adv") {
          obj.content = obj.content.replace(/<img[^>]+>/g, "");
          obj.src = Url + "adv/" + obj.src;
        } else
          obj.content = obj.content.replace(
            /<img src="\//g,
            '<img src="' + Url
          );
        this.obj = obj;
      });
    },
    showBig(src) {
      this.close();
      this.flag = true;
      const img = document.createElement("img");
      img.src = src;
      this.img = img;
      const oB = this.$refs.detail;
      oB.appendChild(img);
    },
    close() {
      if (this.img) {
        const oB = this.$refs.detail;
        oB.removeChild(this.img);
        this.img = null;
        this.flag = false;
      }
    }
  },
  watch: {
    $route() {
      const path = this.$route.path;
      if (path === "/detail") {
        this.query = this.$route.query;
        this.getObj();
      }
    }
  },
  filters: {
    format(d) {
      const date = new Date(d);
      return date.toLocaleDateString().replace(/\//g, "-");
    }
  },
  components: {
    Panel
  }
};
</script>
