import Vue from 'vue'
import Vuex from 'vuex'
import Axios from 'axios'
import { Url } from '../components/public'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    msgArr: []
  },
  getters: {
    getArr(state) {
      return state.msgArr
    }
  },
  mutations: {
    getObj(state, arr) {
      state.msgArr = arr;
    }
  },
  actions: {
    data(context, type) {
      return new Promise((resovle, reject) => {
        Axios.get(Url + 'users/getMsgObj').then(rs => {
          const arr = rs.data;
          context.commit('getObj', arr);
          if (type === 'media')
            resovle({ video: arr[3], images: arr[2] });
          else
            resovle({ newest: arr[0], newhot: arr[1] });
          // reject('请求数据失败')
        })
      })
    }
  },
  modules: {
  }
})
