<template>
  <div class="media">
    <h3>
      {{headTxt}}
      <router-link :to="'/msgList/'+type">
        More
        <i class="el-icon-d-arrow-right"></i>
      </router-link>
    </h3>
    <section ref="section" @mouseenter="stop" @mouseleave="play">
      <ul ref="ul" :style="{left}">
        <li v-for="(el,i) in arrBig" :key="i">
          <img :src="url+el.src" @load="loaded" :ref="'imgName'+i" />
          <router-link :to="{path:'/detail',query:{type,id:el.id}}">{{el.title}}</router-link>
        </li>
      </ul>
      <div class="left" @click="rightMove">
        <span></span>
      </div>
      <div class="right" @click="leftMove">
        <span></span>
      </div>
      <ol>
        <li v-for="i in Len" :key="i" @click="jumpMove(i)" :class="{on:curPhoto===i}"></li>
      </ol>
    </section>
  </div>
</template>
<script>
import { Url } from "./public";
export default {
  name: "media",
  data() {
    return {
      headTxt: "",
      arrBig: [],
      url: "",
      Len: 0,
      count: 0,
      curPhoto: 1,
      left: "-100%",
      timer: null,
      width: 0,
      oUl: null,
      colse: false
    };
  },
  props: ["type"],
  mounted() {
    let arr = this.$store.state.msgArr;
    if (arr.length === 0) {
      this.$store.dispatch("data",'media').then(obj => {
        this.dataProc(obj)
      });
    }else{
      const obj={video:arr[3],images:arr[2]};
      this.dataProc(obj)      
    }
  },
  methods: {
    dataProc(rs) {
      let arr=[];
      if (this.type === "Video") {
        this.headTxt = "视频新闻";
        this.url = Url + "video/";
        arr = rs.video;
      } else {
        this.headTxt = "图片新闻";
        this.url = Url + "photoes/";
        arr = rs.images;
      }
      this.Len = arr.length;
      this.arrBig = [...arr];
      this.arrBig.unshift(arr[3]);
      this.arrBig.push(arr[0]);
      const section = this.$refs.section;
      this.section = section;
      const width = this.getStyle(section, "width");
      this.width = width;
      const ul = this.$refs.ul;
      this.oUl = ul;
      ul.style.width = width * (this.Len + 2) + "px";
      this.play();
    },
    stop() {
      clearInterval(this.timer);
    },
    play() {
      this.timer = setInterval(_ => {
        this.leftMove();
      }, 3000);
    },
    leftMove() {
      if (this.colse) return;
      this.curPhoto++;
      this.move();
    },
    rightMove() {
      if (this.colse) return;
      this.curPhoto--;
      this.move();
    },
    jumpMove(i) {
      if (this.colse || this.curPhoto === i) return;
      this.curPhoto = i;
      this.move();
    },
    move() {
      this.colse = true;
      this.motion(-this.curPhoto * this.width, () => {
        if (this.curPhoto >= this.Len + 1) {
          this.left = -this.width + "px";
          this.curPhoto = 1;
        } else if (this.curPhoto <= 0) {
          this.left = -this.Len * this.width + "px";
          this.curPhoto = this.Len;
        }
        this.colse = false;
      });
    },
    motion(end, fn) {
      const obj = this.oUl;
      clearInterval(obj.timer);
      obj.timer = setInterval(_ => {
        var current = this.getStyle(obj, "left");
        var speed = (end - current) / 10;
        speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed);
        if (current != end) {
          this.left = current + speed + "px";
        } else {
          clearInterval(obj.timer);
          if (fn) fn();
        }
      }, 30);
    },
    getStyle(obj, attr) {
      if (obj.currentStyle) {
        return parseInt(obj.currentStyle[attr]);
      } else {
        return parseInt(getComputedStyle(obj, false)[attr]);
      }
    },
    loaded() {
      this.count++;
      if (this.count === this.Len + 2) {
        const ul = this.$refs.ul;
        const height = this.getStyle(ul, "height");
        this.section.style.height = height + 65 + "px";
      }
    }
  }
};
</script>
