<template>
  <div class="list">
    <article>
      <el-row :gutter="30">
        <el-col :span="16">
          <section v-if="clsType==='Notice'" class="notice">
            <h2>
              {{headTxt}}
              <a href="#/">
                <i class="el-icon-d-arrow-left"></i>首页
              </a>
            </h2>
            <ul>
              <li v-for="(el,i) in arr" :key="el.id">
                <router-link :to="{path:'/detail/',query:{type,id:el.id}}">
                  <h4>{{i+1+"、"+el.title}}</h4>
                  <p>{{el.updatedAt|format}}</p>
                </router-link>
              </li>
              <li>
                <el-pagination
                  @current-change="curChagne"
                  background
                  layout="prev, pager, next"
                  :total="total"
                ></el-pagination>
              </li>
            </ul>
          </section>
          <section v-else>
            <h2>
              {{headTxt}}
              <a href="#/">
                <i class="el-icon-d-arrow-left"></i>首页
              </a>
            </h2>
            <ul>
              <li v-for="el in arr" :key="el.id">
                <router-link :to="{path:'/detail/',query:{type,id:el.id}}">
                  <h4>{{el.title}}</h4>
                  <figure>
                    <img :src="el.src" alt />
                    <figcaption>
                      <p class="list_text">{{el.content}}</p>
                      <p class="timer">
                        {{el.updatedAt|format}}
                        <i>{{timer}}次</i>
                      </p>
                    </figcaption>
                  </figure>
                </router-link>
              </li>
              <li>
                <el-pagination
                  @current-change="curChagne"
                  background
                  layout="prev, pager, next"
                  :total="total"
                ></el-pagination>
              </li>
            </ul>
          </section>
        </el-col>
        <el-col :span="8">
          <Panel />
        </el-col>
      </el-row>
    </article>
  </div>
</template>
<script>
import { Url } from "../components/public";
import Panel from "../components/Panel";
export default {
  name: "List",
  data() {
    return {
      type: "",
      headTxt: "",
      start: 0,
      arr: [],
      total: 0,
      clsType: "",
      timer:0,
    };
  },
  mounted() {
    this.type = this.$route.params.type;
    this.getTotal();
    this.getArr();
    this.$axios.get('userVisit').then(rs=>{
      this.timer=rs;
    })
  },
  methods: {
    getTotal() {
      let headTxt, clsType;
      const type = this.type;
      console.log(type);
      
      switch (type) {
        case "headlines":
          headTxt = "关注头条";
          clsType = "news";
          break;
        case "comprehensive":
          headTxt = "综合新闻";
          clsType = "news";
          break;
        case "learning":
          headTxt = "学术动态";
          clsType = "news";
          break;
        case "story":
          headTxt = "师大故事";
          clsType = "news";
          break;
        case "notice":
          headTxt = "通知公告";
          clsType = "Notice";
          break;
        case "Video":
          headTxt = "视频新闻";
          clsType = "Video";
          break;
        case "Images":
          headTxt = "图片新闻";
          clsType = "Images";
          break;
        case "newest":
          headTxt = "最新新闻";
          clsType = "news";
          break;
        case "newhot":
          headTxt = "热点新闻";
          clsType = "news";
          break;
        case "adv":
          headTxt = "广告新闻";
          clsType = "Adv";
          break;
      }
      this.headTxt = headTxt;
      this.clsType = clsType;
      const params = { params: { clsType, type } };
      this.$axios.get("getListTotal", params).then(number => {
        this.total = number;
      });
    },
    getArr() {
      const clsType = this.clsType;
      const params = {
        params: { start: this.start, clsType, type: this.type }
      };
      this.$axios.get("getListMsg", params).then(arr => {
        switch (clsType) {
          case "news":
            arr = arr.map(el => {
              let imgArr = el.content.match(/src="[^"]+/);
              el.src = imgArr[0].substr(5);
              el.content = el.content.replace(/<[^>]+>/g,'');
              return el;
            });
            break;
          case "Video":
            arr = arr.map(el => {
              el.src = Url + "video/" + el.src;
              el.content = el.content.replace(/<[^>]+>/g,'');
              return el;
            });
            break;
          case "Images":
            arr = arr.map(el => {
              el.src = Url + "photoes/" + el.src;
              el.content = el.content.replace(/<[^>]+>/g,'');
              return el;
            });
            break;
          case "Adv":
            arr = arr.map(el => {
              el.src = Url + "adv/" + el.src;
              el.content = el.content.replace(/<[^>]+>/g,'');
              return el;
            });
            break;
        }
        this.arr = arr;
      });
    },
    curChagne(i) {
      this.start = (i - 1) * 10;
      this.getArr();
    }
  },
  watch: {
    $route() {
      let path = this.$route.path;
      path = path.split("/")[1];
      if (path === "msgList") {
        this.type = this.$route.params.type;
        this.getTotal();
        this.getArr();
        this.curChagne(1);
      }
    }
  },
  filters: {
    format(str) {
      let d = new Date(str).toLocaleDateString();
      return d.replace(/\//g, "-");
    }
  },
  components: {
    Panel
  }
};
</script>
