<template>
  <div class="newlist">
    <h3>
      {{headTxt}}
      <router-link :to="'/msgList/'+type">
        More
        <i class="el-icon-d-arrow-right"></i>
      </router-link>
    </h3>
    <ul>
      <li v-for="el in arr" :key="el.id">
        <router-link :title="el.title" :to="{path:'/detail',query:{type,id:el.id}}">
          <aside>
            <span class="date">{{el.updatedAt | date}}</span>
            <span class="ym">{{el.updatedAt | yearMonth}}</span>
          </aside>
          <h4>{{el.title}}</h4>
          <i>1186次</i>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "newlist",
  data() {
    return {
      headTxt: "",
      arr: []
    };
  },
  props: ["type"],
  mounted() {
    let arr = this.$store.state.msgArr;
    if (arr.length === 0) {
      this.$store.dispatch("data",'newlist').then(obj => {
        this.dataProc(obj);
      });
    } else {
      const obj = { newest: arr[0], newhot: arr[1] };
      this.dataProc(obj);
    }
  },
  methods: {
    dataProc(obj) {
      let arr = [];
      if (this.type === "newest") {
        this.headTxt = "最新新闻";
        arr = obj.newest;
      } else {
        this.headTxt = "热点新闻";
        arr = obj.newhot;
      }
      this.arr = arr;
    }
  },
  filters: {
    date(d) {
      return new Date(d).getDate();
    },
    yearMonth(d) {
      const date = new Date(d);
      return date.getMonth() + 1 + "-" + date.getFullYear();
    }
  }
};
</script>
