<template>
  <div class="search">
    <article>
      <el-row :gutter="30">
        <el-col :span="16">
          <aside>
            <input type="text" v-model="keyword" />
            <select v-model="type">
              <option v-for="el in arrType" :value="el.value" :key="el.value">{{el.text}}</option>
            </select>
            <button @click="search">搜索</button>
          </aside>
          <section v-show="arr.length!=0">
            <h2>
              {{headTxt}}
              <a href="#/">
                <i class="el-icon-d-arrow-left"></i>首页
              </a>
            </h2>
            <ul>
              <li v-for="(el,i) in arr" :key="el.id">
                <router-link :to="{path:'/detail/',query:{type,id:el.id}}">
                  <h4>{{i+1+"、"+el.title}}</h4>
                  <p>{{el.updatedAt|format}}</p>
                </router-link>
              </li>
              <li>
                <el-pagination
                  @current-change="curChagne"
                  background
                  layout="prev, pager, next"
                  :total="total"
                ></el-pagination>
              </li>
            </ul>
          </section>
        </el-col>
        <el-col :span="8">
          <Panel />
        </el-col>
      </el-row>
    </article>
  </div>
</template>
<script>
import { Url } from "../components/public";
import Panel from "../components/Panel";
export default {
  name: "Search",
  data() {
    return {
      type: "-1",
      keyword: "",
      headTxt: "",
      start: 0,
      arr: [],
      total: 0,
      arrType: [
        {
          value: "-1",
          text: "--请选择--"
        },
        {
          value: "headlines",
          text: "头条关注"
        },
        {
          value: "comprehensive",
          text: "综合新闻"
        },
        {
          value: "learning",
          text: "学术动态"
        },
        {
          value: "story",
          text: "师大故事"
        },
        {
          value: "notice",
          text: "通知公告"
        },
        {
          value: "Video",
          text: "视频新闻"
        },
        {
          value: "Images",
          text: "图片新闻"
        },
        {
          value: "newest",
          text: "最新新闻"
        },
        {
          value: "newhot",
          text: "热点新闻"
        }
      ]
    };
  },
  methods: {
    search() {
      const type = this.type;
      const keyword = this.keyword.trim();
      if (type === "-1" || keyword === "") {
        alert("关键词不能空或者类别需要选择");
        return;
      }
      let tableName = "";
      switch (type) {
        case "headlines":
          this.headTxt = "关注头条";
          tableName = "News";
          break;
        case "comprehensive":
          this.headTxt = "综合新闻";
          tableName = "News";
          break;
        case "learning":
          this.headTxt = "学术动态";
          tableName = "News";
          break;
        case "story":
          this.headTxt = "师大故事";
          tableName = "News";
          break;
        case "notice":
          this.headTxt = "通知公告";
          tableName = "Notice";
          break;
        case "Video":
          this.headTxt = "视频新闻";
          tableName = "Video";
          break;
        case "Images":
          this.headTxt = "图片新闻";
          tableName = "Images";
          break;
        case "newest":
          this.headTxt = "最新新闻";
          tableName = "News";
          break;
        case "newhot":
          this.headTxt = "热点新闻";
          tableName = "News";
          break;
      }
      this.tableName = tableName;
      //获取搜索总记录数
      let params;
      if (tableName === "News") {
        params = { params: { tableName, type, keyword } };
      } else {
        params = { params: { tableName, keyword } };
      }
      this.$axios.get("getSearchTotal", params).then(number => {
        this.total = number;
        this.getArr();
      });
    },
    getArr() {
      //获取当前页数据
      const tableName = this.tableName;
      const start = this.start;
      const type = this.type;
      const keyword = this.keyword;
      let params;
      if (tableName === "News") {
        params = { params: { start, tableName, type, keyword } };
      } else {
        params = { params: { start, tableName, keyword } };
      }
      this.$axios.get("getSearchMsg", params).then(arr => {
        this.arr = arr;
      });
    },
    curChagne(i) {
      this.start = (i - 1) * 5;
      this.getArr();
    }
  },
  filters: {
    format(str) {
      let d = new Date(str).toLocaleDateString();
      return d.replace(/\//g, "-");
    }
  },
  components: {
    Panel
  }
};
</script>
